import * as React from 'react';

const step1 = <div>Join our discord (<a href="https://discord.gg/yestheory" className="inline-blue">https://discord.gg/yestheory</a>). The server is where most of the planning and conversation takes place. It's a fun place to hang out and once you get into it, it will be hard to leave!</div>;
const step2 = <div>Once you join the server, drop the country youre from in <div className="inline-blue">#where-are-you-from</div> so you can be given full access to all the fun chats! You can take a look at <div className="inline-blue">#general-info</div> to learn everything you need to know about the server, and dont forget to also read the <div className="inline-blue">#rules.</div></div>;
const step3 = <div>If you scroll down the list of channels, youll see a category called FIYESTAS. Thats where everything you need to know about those meetups is! Click on <div className="inline-blue">#fiyesta-guidelines</div> to learn the general requirements of all FiYEStas.</div>;
const step4 = <div>Next, you can head to <div className="inline-blue">#fiyesta-bulletin-board</div> to see the full list of FiYEStas. Find the one youd like to join, and message the person to contact. Youre almost there! Once they give you the okay, theyll let a Support know, and youll be given access to the FiYESta chat.</div>;

export default [step1, step2, step3, step4];

